#projects {
  display: flex; 
  flex-direction: column; 
}


.card {
  border:none;
  margin: 1rem;
  width: 100%;
}



.card-horizontal {
  display: flex;
  flex-direction: column;
  background-color: whitesmoke;
}

@media screen and (min-width: 850px){
  .card-horizontal {
    flex-direction: row;
  }
}

.btn-light {
  background-color: lightgray;
  margin: 0.2rem;
}

#tools {
  color: darkslategray;
}

