

h1 {
  font-size: 4em;
}

h2 {
  font-size: 3em;
}

h3 {
  font-size: 2.4em;
}

h4 {
  font-size: 2em;
}

h5 {
  font-size: 1.7em;
}

p {
  font-size: 1.4em; 
}

li {
  font-size: 1.5em;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.fade-exit {
  opacity: 0;
}

html { 
  overflow-y:scroll;
  overflow-x:hidden;
}

#main {
  padding: 8%;
  margin-bottom: 8%;
}

#appRow {
  display: flex;  
  flex-direction: column;
  justify-content: space-between;
}

.col-4-custom {
  width: 100%; 
}

.col-8-custom {
  width: 100%;
}

@media screen and (min-width: 850px){
  #main {
    padding: 3% 3% 0 0;
  }

  #appRow {
    display: flex; 
    flex-direction: row; 
  }

  .col-4-custom {
    width: 30%; 
  }
  
  .col-8-custom {
    width: 70%;
  }

  
}



